import { Contract } from 'web3'
import {
  AbiContract,
  AbiJsonInterface,
  AbiJsonParam,
  BlockNumber,
} from '../../core'
import {
  Web3CallData,
  Web3ClientModel,
  Web3ContractMethod,
  Web3ContractMethodParam,
  Web3DataParam,
} from '../../web3-client'
import { WalletContractData } from '../wallet.model'
import { ERC20_ABI } from '../../vaults'

export class WalletService {
  public address: string
  public web3Client: Web3ClientModel

  constructor(address: string, web3Client: Web3ClientModel) {
    this.address = address
    this.web3Client = web3Client
  }

  public async getBalance(contractAddress: string, blockNumber?: BlockNumber) {
    const contract = this.web3Client.initContract(ERC20_ABI, contractAddress)

    const contractMethod: Web3ContractMethod = {
      protocol: 'Idle',
      type: 'WALLET',
      block: 'current',
      method: 'balanceOf',
      params: ['walletAddress'],
    }

    const callData = this.makeMethodData(contract, contractMethod)
    const responses = await this.web3Client.call([callData], blockNumber)
    return this.parseCallResponses(responses)
  }

  public async getAllowance(
    tokenAddress: string,
    spenderAddress: string,
    blockNumber?: BlockNumber
  ) {
    const contract = this.web3Client.initContract(ERC20_ABI, tokenAddress)

    const contractMethod: Web3ContractMethod = {
      type: 'WALLET',
      protocol: 'Idle',
      block: 'current',
      method: 'allowance',
      params: ['walletAddress', 'spenderAddress'],
    }

    const callData = this.makeMethodData(contract, contractMethod, {
      spenderAddress,
    })
    const responses = await this.web3Client.call([callData], blockNumber)
    return this.parseCallResponses(responses)
  }

  /**
   * Parse method into Web3CallData
   * @param method - the contract method
   * @returns the web3CallData
   */
  protected makeMethodData(
    contract: Contract<AbiContract>,
    contractMethod: Web3ContractMethod,
    values?: any
  ): Web3CallData {
    const { jsonInterface } = contract.options
    const { protocol, type, method, block, params = [] } = contractMethod
    const address = contract.options.address

    if (!address) {
      throw new Error('Contract without a valid address')
    }

    // Get ABI method to prepare the right types
    const methodAbi = (jsonInterface as AbiJsonInterface[]).find(
      (f) => f.name === method && f.inputs.length === params.length
    )

    if (!methodAbi) {
      throw new Error(
        `No ABI method '${method}' found for ${protocol} at contract ${contract.options.address}`
      )
    }

    // Method name + params
    const inputTypes = methodAbi.inputs.map((i) => i.type)
    const methodName = `${methodAbi.name}(${inputTypes.join(',')})`

    // Input & Outputs
    const inputs: Web3DataParam[] = methodAbi.inputs.map((input, i) =>
      this.makeMethodParamData(input, params[i], values)
    )
    const outputs: Web3DataParam[] = methodAbi.outputs.map((output) => ({
      type: output.type,
      name: output.name,
      components: output.components,
    }))

    return {
      protocol,
      type,
      address,
      method: methodName,
      block,
      inputs,
      outputs,
    }
  }

  /**
   * Parse vault json param
   * @param vault - the vault
   * @param input - the ABI Json Param
   * @returns the vault data param
   */
  protected makeMethodParamData(
    input: AbiJsonParam,
    param?: Web3ContractMethodParam,
    values?: any
  ): Web3DataParam {
    const type = input.type

    return {
      type,
      value: param ? this.parseMethodParam(param, values) : undefined,
    }
  }

  /**
   * Parse method param
   * @param param - the method param
   * @returns the value of the param
   */
  private parseMethodParam(param: Web3ContractMethodParam, values?: any): any {
    let value

    switch (param) {
      case 'walletAddress':
        value = this.address
        break
      case 'spenderAddress':
        value = values ? values['spenderAddress'] : undefined
        break
    }

    return value
  }

  /**
   * Parse Web3CallData into contract data
   * @param response - the response from web3call data
   * @returns the vault contract data
   */
  protected parseCallResponses(responses: Web3CallData[]): WalletContractData {
    return responses.reduce<WalletContractData>(
      (acc, res) => this.parseCallResponse(acc, res),
      {}
    )
  }

  /**
   * Parse call response
   * @param data - the already processed data
   * @param response - the call response
   * @returns the contract data
   */
  private parseCallResponse(
    data: WalletContractData,
    { method, outputs }: Web3CallData
  ): WalletContractData {
    const methodName = method.split('(')[0]
    const methodData: WalletContractData = {}

    switch (methodName) {
      case 'balanceOf':
        methodData.balance = outputs[0].value
        break
      case 'allowance':
        methodData.allowance = outputs[0].value
        break
    }

    return { ...data, ...methodData }
  }
}
